
@import "../_component.scss";


.screen_out{
	position: absolute;
	width: 0;
	height: 0;
	font: 0/0 a;
}
.icon-menu{
	width: 17px;
	height: 3px;
	background-color: #000;
	&:before,
	&:after{
		content: '';
		position: absolute;
		left: 0;
		display: inline-block;
		width: 17px;
		height: 3px;
		background-color: #000;
	}
	&:before{
		top:0;
	}
	&:after{
		bottom:0;
	}
}
.icon-larr{
	width: 4px;
	height: 4px;
	border-top: 2px solid #0061a0;
	border-left: 2px solid #0061a0;
	border-radius: 1px;
	transform: rotate(-45deg);
}
.icon-exclamation:after{
	content: '!';
}
.icon-addfile{
	font: 0/0 a;
	margin: 0 3px;
}
.is-mobile{
	display: none;
}
.pull-left{
	float: left;
}
.pull-right{
	float: right;
}

.text-center{
	padding-left: 0;
	text-align: center;
}

.text-danger{
	color: #eb4646;
}

.fc-primary{
	color: #0074b2;
}
.fc-dark{
	color: #333;
}
.fc-light{
	color: #888;
}

.link{
	color: #0074b2;
}

h4{
	padding-top: 25px;
	padding-bottom: 15px;
	padding-left: 20px;
	font-weight: 700;
	font-size: 16px;
}

h5{
	padding-top: 15px;
	padding-bottom: 25px;
	font-weight: 400;
	font-size: 16px;
}

.hidden{
	display: none;
}

.inform-msg{
	position: relative;
	margin-top: 65px;
	margin-left: 20px;
	padding-top: 0;
	font-size: 14px;
	line-height: 1.5;
	color: #888;
	&:before{
		content: 'â€»';
		position: absolute;
		top: 0;
		left: 0;
	}
	em{
		display: inline-block;
		padding-left: 18px;
		letter-spacing: -.5px;
	}
}

.heading{
	padding-bottom: 12px;
	border-bottom: 1px solid #000;
	@include clear;
	h3{
		float: left;
		padding-left: 20px;
		color: #333;
		font-size: 24px;
		line-height: 1.75;
		letter-spacing: -.5px;
		time{
			font-size: 14px;
		}
	}
	time{
		display: inline-block;
		margin-bottom: 4px;
		vertical-align: bottom;
		font-size: 16px;
	}
	&-etc{
		float: right;
		.calendar{
			.datepicker{
				max-width: 121px;
			}
		}
	}
}